.switcher {
  display: inline-flex;
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  color: #3e4056;
}

.switcher__input {
  display: none;
  height: 0;
  width: 0;
  visibility: hidden;
}

.switcher__input:checked + .switcher__box {
  background-color: #931b29;
  transition: all 0.3s ease;
}

.switcher__input:checked + .switcher__box:before {
  background-color: #ffffff;
  left: calc(100% - 2px);
  transform: translateX(-100%) translateY(-50%);
  transition: all 0.3s ease;
}

.switcher__box {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 24px;
  background: #ffffff;
  position: relative;
  border-radius: 50px;
  border: 1px solid #931b29;
  cursor: pointer;
  transition: all 0.3s ease;
  margin: 0 30px;
}

.switcher__box:before {
  content: '';
  display: inline-block;
  width: 18px;
  height: 18px;
  border-radius: 50px;
  background-color: #931b29;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 2px;
  transition: all 0.3s ease;
}

.switcher__box:active:before {
  width: 24px;
}

.drop {
  position: relative;
}

.drop__wrap:hover .drop__box {
  visibility: visible;
  transition: all 0.3s ease;
}

.drop__box {
  position: absolute;
  top: calc(100% + 18px);
  min-width: 176px;
  visibility: hidden;
  background-color: rgba(255, 255, 255, 0.94);
  border-radius: 8px;
  min-height: 124px;
  padding: 24px 0;
  overflow-y: auto;
  transition: all 0.3s ease;
  font-size: 16px;
  right: 0;
}

@media (min-width: 768px) {
  .drop__box {
    right: auto;
    left: 0;
  }
}

.drop__box li:hover {
  background-color: #ecf1f8;
}

.drop__box a {
  display: flex;
  align-items: center;
  padding: 8px 24px;
  color: #3e4056;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
}

.drop__box a i {
  padding-right: 16px;
}

.card {
  display: grid;
  grid-template-rows: [head] 32px [main] minmax(0, 1fr) [foot] 32px;
  grid-template-columns: minmax(0, 1fr);
  min-height: 280px;
  box-shadow: 0px 12px 12px rgba(0, 0, 0, 0.06);
  padding: 24px;
  background-color: #ffffff;
  border-radius: 8px;
  overflow: hidden;
}

.card__head {
  grid-row: head;
  display: flex;
  align-items: center;
  font-size: 16px;
  line-height: 22px;
  font-weight: 600;
  white-space: nowrap;
}

.card__main {
  grid-row: main;
  padding: 10px 0;
}

.card__foot {
  grid-row: foot;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-tag {
  background: #ecf1f8;
  padding: 2px 4px;
  border-radius: 6px;
  font-weight: 600;
  font-size: 16px;
  line-height: 16px;
  color: #3e4056;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-left: 4px;
}

.card-text {
  font-size: 36px;
  line-height: 44px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  text-overflow: ellipsis;
}

.card-link {
  color: #931b29;
  font-size: 18px;
  line-height: 25px;
}

.card-actions {
  display: flex;
  margin: 0 -7px;
}

.card-actions li {
  padding: 7px;
}

.card-actions__btn {
  color: #3e4056;
}

.card-actions__btn i {
  padding-right: 9px;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
}

.card-actions__btn.primaryliked i {
  color: #931b29;
}

.card-share a {
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  color: #3e4056;
}

.card-share i {
  font-size: 16px;
  padding-right: 6px;
}

.card-profile {
  display: flex;
  align-items: center;
  color: #3e4056;
}

.card-profile__img {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #4d4d56;
  overflow: hidden;
  flex-shrink: 0;
}

.card-profile__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card-profile__name {
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  padding-left: 8px;
}

