.header {
  background: #ffffff;
  box-shadow: 0px 12px 12px rgba(0, 0, 0, 0.06);
  position: relative;
}

.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}

.header-right {
  display: flex;
  align-items: center;
}

.header-profile {
  display: flex;
  align-items: center;
