}

@media (min-width: 768px) {
  .header-profile {
    padding-right: 28px;
  }
}

.header-profile__img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: #3e4056;
  margin: 0 10px 0 16px;
  overflow: hidden;
}

.header-profile__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header-profile__name {
  display: none;
}

@media (min-width: 768px) {
  .header-profile__name {
    display: block;
  }
}

.header-menu {
  display: flex;
  margin: 0 -5px;
}

.header-menu li {
  font-size: 20px;
  padding: 0 5px;
}

.header-menu a {
  color: #3e4056;
}

.header-bottom {
  position: relative;
}

.header-pages {
  display: flex;
  margin: 0 -5px;
}

@media (min-width: 768px) {
  .header-pages {
    margin: 0 -19px;
  }
}

.header-pages li {
  padding: 0 5px;
}

@media (min-width: 768px) {
  .header-pages li {
    padding: 0 19px;
  }
}

.header-pages a {
  position: relative;
  display: block;
  font-weight: 700;
  color: rgba(62, 64, 86, 0.6);
  padding-bottom: 10px;
}

@media (min-width: 768px) {
  .header-pages a {
    padding-bottom: 23px;
  }
}

.header-pages a:before {
  position: absolute;
  content: "";
  height: 2px;
  left: 0;
  right: 0;
  background: linear-gradient(135deg, #ff974f 0%, #e32c75 100%);
  bottom: 0;
  opacity: 0;
  visibility: hidden;
}

.header-pages a:hover, .header-pages a.active {
  color: #3e4056;
}

.header-pages a:hover:before, .header-pages a.active:before {
  visibility: visible;
  opacity: 1;
}

.header-add {
  position: absolute;
  right: 0;
  border-radius: 50%;
  background: linear-gradient(135deg, #ff974f 0%, #e32c75 100%);
  bottom: -16px;
  width: 32px;
  height: 32px;
}

@media (min-width: 768px) {
  .header-add {
    bottom: -32px;
    height: 64px;
    width: 64px;
  }
}

.header-add:before, .header-add:after {
  content: "";
  position: absolute;
  background-color: #ffffff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.3s ease;
}

.header-add:before {
  width: 2px;
  height: 10px;
}

@media (min-width: 768px) {
  .header-add:before {
    height: 20px;
  }
}

.header-add:after {
  height: 2px;
  width: 10px;
}

@media (min-width: 768px) {
  .header-add:after {
    width: 20px;
  }
}

.header-add:hover:before, .header-add:hover:after {
  transform: translate(-50%, -50%) rotate(180deg);
  transition: all 0.3s ease;
}

