  color: #200e32;
  flex-shrink: 0;
  font-size: 17px;
}

.personal-profile__content {
  display: flex;
  flex-direction: column;
  padding-left: 24px;
  flex-grow: 1;
}

.personal-profile__head {
  display: flex;
  align-items: flex-start;
}

.personal-profile__btn {
  margin-top: auto;
  text-align: left;
  color: #931b29;
  font-size: 16px;
  line-height: 20px;
  text-decoration-line: underline;
}

.infoprofile {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;
  grid-row-gap: 23px;
  padding-top: 32px;
}

.infoprofile__info {
  font-size: 16px;
  line-height: 24px;
  color: #4d4d56;
}

.infoprofile__info strong {
  color: #000000;
  margin-left: 15px;
}

.infoprofile__text {
  grid-column: 1/4;
  grid-row: 2/3;
  font-size: 16px;
  line-height: 24px;
  color: #3e4056;
  text-align: left;
}
